import React, { Component } from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { Tabs, Tab, TabPanel, TabList } from 'react-tabs';

import './Profile.css';

class Profile extends Component {
  constructor(){
    super();
    this.state={
      tabIndex: 0,

      profile_name: '', //  
      phone_number: '', //  
      password: null, // 
      passwordC: null,  //  
      exchange: [], //   

      selectedExchange: '', //   

      pVal: false, //  
      ppVal: false //   
    }
  }

  //         
  componentDidMount() {
    //   =============================================================================================================================
    // var response = {
    //   data: {"name":"","phone_number":"01053265984","exchange":[{"exchange_name":"BINANCE","secret_key":"VsYgQmXENsOwBhVIMnrsvtuBWwn568t5AFRlFHO3bZd47WzXUFBxMRPrTN3TYNAH","api_key":"UMjZpOB0UNMMRRh697FgDiUb81vyKLlOey7fQeeBAM9MoOOoNgDAWB0PpjJNwvWe"},{"exchange_name":"BITHUMB","secret_key":"8f0d6ad5a93ca501061e73e18811bf3c","api_key":"68c761b96fb41cceb9366e145f6b161c"},{"exchange_name":"COINONE","secret_key":'',"api_key":''}]}
    // }
    // this.setState({
    //   name: response.data.name,
    //   phone_number: response.data.phone_number,
    //   exchange: response.data.exchange,
    //   selectedExchange: response.data.exchange[0]
    // })
    // =========================================================================================================================================
    axios.get('Profile')
    .then( response => {
      this.setState({
        profile_name: response.data.name,
        phone_number: response.data.phone_number,
        exchange: response.data.exchange, //  DB     ,   redux store     
  
        selectedExchange: response.data.exchange[0]
      })
    }) 
    .catch( response => { console.log('err\n'+response); } ); // ERROR
  }

  //    (     )
  handleExchange = () => {
    this.state.exchange.map((e, i) => {
      e.exchange_name === document.getElementById('exchange').value
      && this.setState({ selectedExchange: e })
    })
  }

  //     
  handleValue = (e) => {
    switch(e.target.id){
      case 'profile_name':  // 
        return this.setState({ profile_name: e.target.value })
      case 'phone_number':  // 
        return (!isNaN(e.target.value)) && this.setState({ phone_number: e.target.value })
      case 'newPassword': //    (( ),, ~!@#$%^&*()-_?  )
        return (e.target.value.match(/^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,16}$/) && (e.target.value.length >= 8) && (e.target.value.length <= 16)) 
          ? this.setState({ password: e.target.value, pVal: true })
          : this.setState({ password: e.target.value, pVal: false })
      case 'newPasswordConfirm':  //  
        return (this.state.password === e.target.value)
          ? this.setState({ passwordC: e.target.value, ppVal: true })
          : this.setState({ passwordC: e.target.value, ppVal: false })
      case 'api_key':
        return this.setState({ selectedExchange: { ...this.state.selectedExchange, api_key: e.target.value } })
      case 'secret_key':
        return this.setState({ selectedExchange:{ ...this.state.selectedExchange, secret_key: e.target.value } })
      default:
    }
  }

  //   
  handleModPersonal = () => {
    if(window.confirm('?')){
      axios.post(
        'Profile', 
        'item=profile'+
        '&name='+document.getElementById('profile_name').value+
        '&phone_number='+document.getElementById('phone_number').value,
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        this.setState({
          profile_name: response.data.name,
          phone_number: response.data.phone_number
        })
        alert(' .')
      }) 
      .catch( response => { console.log('err\n'+response); } ) // ERROR
    }
  }

  //  
  handleModPassword = () => {
    if(this.state.pVal && this.state.ppVal){
      if(window.confirm('?')){
        axios.post(
          'Profile', 
          'item=password'+
          '&old_password='+document.getElementById('oldPassword').value+
          '&password='+this.state.password,
          { 'Content-Type': 'application/x-www-form-urlencoded' }
        )
        .then( response => {
          switch(response.data){
            case 'wrongError':
              return alert('   .')
            case 'sameError':
            return alert('    .')
            default: return alert(' .')
          }
        }) 
        .catch( response => { console.log('err\n'+response); } ) // ERROR
      
      }

    }
  }
  
  //   
  handleModExchange = () => {
    if(window.confirm('?')){
      axios.post(
        'Profile', 
        'item=exchange'+
        '&exchange_name='+document.getElementById('exchange').value+
        '&api_key='+document.getElementById('api_key').value+
        '&secret_key='+document.getElementById('secret_key').value,
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        this.setState({
          exchange: response.data.exchange,
          selectedExchange: response.data.exchange[document.getElementById('exchange').selectedIndex]
        })
        alert(' .')
      }) 
      .catch( response => { console.log('err\n'+response); } ) // ERROR    
    }
  }

  handleComplete = () => {
    if(window.confirm('     .')){
      window.location = "/";
    }
  }

  render() {
    const { exchangeList } = this.props
    const { selectedExchange, password, passwordC, pVal, ppVal } = this.state
    
    return (
      <div class="profile">
        <div class="bd_profile"> {/*tab_container*/}
          <Tabs class="profile_tabs" selectedIndex={this.state.tabIndex} onSelect={tabIndex => this.setState({ tabIndex })}>
            <TabList class="profile_tablist">
              <div class="profile_tabs_wrap">
                <Tab><a class="profile_tab_1"></a></Tab>
                <Tab><a class="profile_tab_2"> </a></Tab>
                <Tab><a class="profile_tab_3"> </a></Tab>
              </div>
            </TabList>

            <TabPanel>
              <div class="profile_1">
                <h4 class="personal"> </h4>
                <div class="personal_contents">
                  <div class="personal_name">
                    <input id="profile_name" placeholder="" value={this.state.profile_name} onChange={this.handleValue}/><br/>
                  </div>
                  <div class="personal_number">
                    <input id="phone_number" placeholder=" " value={this.state.phone_number} onChange={this.handleValue}/><br/>
                  </div>
                </div>
                <button id="modPersonal" onClick={this.handleModPersonal}><img src={require('../img/common/btn_04.png')} /></button>
              </div>
            </TabPanel>
            <TabPanel>
              <div class="profile_3">
                <h4 class="personal"> </h4>
                <div class="exchange">
                  <select id="exchange" onChange={this.handleExchange}>
                  {exchangeList.map((exchange, index) => {
                      return (<option key={index} > {exchange.key} </option>)
                  })}
                  </select><br/>
                </div>
                <div class="exchange_contents">
                  <div class="exchange_api">
                      <input id="api_key" placeholder="API KEY" value={selectedExchange.api_key} onChange={this.handleValue}/><br/>
                  </div>
                  <div class="exchange_secret">
                      <input id="secret_key" placeholder="SECRET KEY" value={selectedExchange.secret_key} onChange={this.handleValue}/><br/>
                  </div>
                </div>
                <button id="modExchange" onClick={this.handleModExchange}><img src={require('../img/common/btn_04.png')} /></button><br/><br/><br/>
              </div>
            </TabPanel>
            <TabPanel>
              <div class="profile_2">
                <h4 class="personal"> </h4>
                <div class="pw_contents">
                  <div class="pw_1">
                    <input id="oldPassword" placeholder=" " type="password"/><br/>
                  </div>
                  <div class="pw_2">
                    <input id="newPassword" placeholder=" " type="password" onChange={this.handleValue}/><br/>
                  </div>
                  <div class="pw_3">
                    <input id="newPasswordConfirm" placeholder=" " type="password" onChange={this.handleValue}/><br/>
                  </div>
                  <div class="pw_error">
                    {((password !== '') && (password !== null) && !pVal) && <text id="pw_error">   .<br/></text>}
                    {((passwordC !== '') && pVal && !ppVal) && <text id="pw_error"> .<br/></text>}
                  </div>
                </div>
                <button id="modPassword" onClick={this.handleModPassword}><img src={require('../img/common/btn_04.png')} /></button>
              </div>
            </TabPanel>
          </Tabs>
        </div>
        {/*<button id="completeButton" onClick={this.handleComplete}><img src={require('../img/common/btn_05.png')} /></button>*/}
      </div>

      // css   (  )
      // <div>
      //   <h4> </h4>
      //   : <input id="name" value={this.state.name} onChange={this.handleValue}/><br/>
      //    : <input id="phone_number" value={this.state.phone_number} onChange={this.handleValue}/><br/>
      //   <button id="modPersonal" onClick={this.handleModPersonal}></button>
      //   <h4> </h4>
      //    : <input id="oldPassword" type="password"/><br/>
      //    : <input id="newPassword" type="password" onChange={this.handleValue}/><br/>
      //    : <input id="newPasswordConfirm" type="password" onChange={this.handleValue}/><br/>
      //   {((password !== '') && (password !== null) && !pVal) && <text>   .<br/></text>}
      //   {((passwordC !== '') && pVal && !ppVal) && <text> .<br/></text>}
      //   <button id="modPassword" onClick={this.handleModPassword}></button>
      //   <h4> </h4>
      //   <select id="exchange" onChange={this.handleExchange}>
      //     {exchangeList.map((exchange, index) => {
      //       return (<option key={index} > {exchange.key} </option>)
      //     })
      //     }
      //   </select><br/>
      //   API KEY: <input id="api_key" value={selectedExchange.api_key} onChange={this.handleValue}/><br/>
      //   SECRET KEY: <input id="secret_key" value={selectedExchange.secret_key} onChange={this.handleValue}/><br/>
      //   <button id="modExchange" onClic
      //   k={this.handleModExchange}></button><br/><br/><br/>
      //   <button onClick={this.handleComplete}></button>
      // </div>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    exchangeList: state.exchange.exchangeList,
  };
}

Profile = connect(mapStateToProps)(Profile);

export default Profile;




