import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { connect } from 'react-redux';

import { logout } from '../reducers/logInOut';

import './Header.css';
import mainLogo from '../img/common/logo_01.png'

class Header extends Component {
  HandleLogOut = () => {
    if(window.confirm('?')){
      axios.get( 'LogInOut')
      this.props.onLogout()
    }
  }

  render() {
    return (
      <div className = "header-container">
        <Link to="/"><img src = {mainLogo} className = 'header-logo'/></Link>
        <Link to="/board" className = 'header-board'></Link>
        <Link to="/backtesting" className = 'header-bt'></Link>
        <Link to="/strategy" className = 'header-strategy'></Link>
        <Link to={{
          pathname: "/log",
          state: {
            test: true,
            pppadsf: ''
          }
        }} className = 'header-log'
        ></Link>
        <Link to="/profile" className = 'header-profile'></Link>
        {this.props.login && <button onClick={this.HandleLogOut} className = 'header-logout'></button>}
      </div>
    );
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    onLogout: () => dispatch(logout())
  }
}

let mapStateToProps = (state) => {
  return {
    login: state.logInOut.login
  };
}

Header = connect(mapStateToProps, mapDispatchToProps)(Header);

export default Header;


